// import { apiCashApply, apiUserInfo } from '@/service/index'
export default {
	data() {
		return {
			true_name: '',
			bank: '',
			bank_card: '',
			amount: '0.00',
			detail: {}
		}
	},
	onShow() {
		// this.getUserInfo()
	},
	methods: {
		getUserInfo() {
			apiUserInfo()
				.then((res) => {
					if (res.code == 0) {
						this.detail = res.data
					}
				})
		},
		transferPage() {
			uni.navigateTo({
				url: '/pages/transfer/transfer'
			});
		},
		onSubmit() {
			if (!this.true_name) {
				uni.showToast({
					title: '请填写姓名',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!this.bank) {
				uni.showToast({
					title: '请填写开户行',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!this.bank_card) {
				uni.showToast({
					title: '请填写提现卡号',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!this.amount) {
				uni.showToast({
					title: '请填写提现金额',
					icon: 'none',
					duration: 2000
				});
				return
			}
			apiCashApply({
					name: this.true_name,
					bank: this.bank,
					bank_card: this.bank_card,
					amount: this.amount
				})
				.then((res) => {
					if (res.code == 0) {
						uni.showToast({
							title: '提交成功',
							duration: 2000
						});
						setTimeout(function() {
							uni.navigateBack({
								delta: 1
							})
						}, 2000)
					}
				})
		}
	}
}
